.navbarsecondary {
  background-color: rgb(35, 47, 62);
  /* height: 32px; */
}
.navbarsecondary ul {
  display: flex;
  text-decoration: none;
  list-style: none;
  color: white;
  padding: 2px;
}
.navbarsecondary li {
  position: relative;
  padding: 5px;
  margin: 0px 5px;
  cursor: pointer;
  /* height: 100%; */
  /* text-align: center; */
}
.navbarsecondary li:hover {
  border: 1px solid white;
}
.navbarsecondary h5 {
  font-size: 10px;
}
a {
  color: white;
}
.menuicon {
  position: relative;
  top: -2px;
}
.menu {
  display: flex;
}
